
.skill{
    display: flex;
    justify-content:center;
    margin-top: 20px;
    font-weight: normal;
    font-family: 'Courier New', Courier, monospace;
    flex-wrap: wrap;



}
.singleSkill{
    border: 1px solid black;
    border-radius: 20px;
    box-shadow:1px 1px 1px #282c34;
    margin: 10px;
}
.singleSkill:hover{
    font-size: 25px;
    transition: ease 1s;
    box-shadow: 1px 1px 9px rgba(233,233,235);
    /*display: block;*/
    filter:drop-shadow(0 0 10px #fffedf);



}
.image{
    width: 70%;
    height: 140px;
}

@media (max-width: 500px) {

    .skill{
        display: flex;
        justify-content:center;
        margin-top: 10px;
        font-weight: normal;
        font-family: 'Courier New', Courier, monospace;
        flex-wrap: wrap;

        width:100%;
        height: 100%;
    }
    .singleSkill{
        border: 1px solid black;
        border-radius: 20px;
        box-shadow:1px 1px 1px #282c34;
        margin: 14px;
    }
    .image{
        width: 50%;
        height: 50%;
        padding: 15px;
    }
}